@mixin desktop {
  @media (min-width: (1200px)) {
    @content;
  }
}

@mixin desktop-1025 {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin tablet-width {
  @media (min-width: (768px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (767px)) {
    @content;
  }
}

@mixin min-mobile {
  @media (min-width: (768px)) {
    @content;
  }
}